import React, { useEffect, useRef, useState } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import './index.scss';
import showdown from 'showdown';
import _ from 'lodash';
import { BsPlus, BsToggleOff, BsToggleOn } from 'react-icons/bs';
import { connect } from 'react-redux';
import { getCategories, createPost } from '@modules/blogs';

const mapStateToProps = (state: any) => ({
    blogs: state.blogs
})

const mapActionsToProps = {
    getCategories: getCategories,
    createPost: createPost
}

function scrollToBottom(element) {
    element.scroll({ top: element.scrollHeight, behavior: 'smooth' });
}

const PostEditor: React.FunctionComponent<any> = (props) => {
    const [togglePopup, setTogglePopup] = useState<boolean>(false);
    const sourceRef = useRef<HTMLTextAreaElement>(null);
    const resultRef = useRef<HTMLTextAreaElement>(null);
    const [toggler, setToggler] = React.useState<boolean>(true);
    const [togglerChanged, setTogglerChanged] = React.useState<boolean>(false);
    const [source, setSource] = React.useState<string>("");
    const [result, setResult] = React.useState<string>("");

    const convertMarkDownToHTML = (text: string) => {
        var converter = new showdown.Converter({
            metadata: true,
            simpleLineBreaks: false,
            openLinksInNewWindow: true,
            emoji: true
        }),
            html = converter.makeHtml(text);
        setSource(text);
        setResult(html);
        return html;
    };

    const debouncedConvertMarkdownToHTML = _.debounce(convertMarkDownToHTML, 500);

    const handleSubmit = (e: any) => {
        e.preventDefault();
        const title = e.target[0].value;
        const subtitle = e.target[1].value;
        const categories = [...e.target[2].options].filter(x => x.selected).map((x: any) => x.value);
        const path = e.target[3].value;
        const cover = e.target[4].value;
        const tags = e.target[5].value.split(",");
        const youtubeEmbedUrl = e.target[6].value;

        const htmlContent = result;
        props?.createPost({
            title,
            subtitle,
            categories,
            path,
            cover,
            tags,
            youtubeEmbedUrl,
            htmlContent
        })

    }
    useEffect(() => {
        if (togglerChanged === true) {
            const tmpSource: any = new String(source);
            const tmpResult: any = new String(result);
            if (sourceRef && sourceRef?.current) {
                sourceRef.current.value = tmpResult;
            }
            if (resultRef && resultRef?.current) {
                resultRef.current.value = tmpSource;
            }
            setSource(tmpResult);
            setResult(tmpSource);
        }

    }, [toggler])

    useEffect(() => {
        props.getCategories();
    }, [])

    return (
        <form onSubmit={handleSubmit} className="post-editor">
            <div className="post-image-editor">

            </div>
            <Container fluid className="post-container">
                <Row className="post-row">
                    <Col lg={6} className="post-column-left">
                        <div className="floating-info">
                            Yeni yazının detayları
                        </div>
                        <div className='border'></div>
                        <div className="inputs">
                            <label htmlFor="title">Title:</label>
                            <input name="title" type="text" className="form-control" placeholder="Enter a title to create a title in article" />

                            <label htmlFor="subtitle">Subtitle:</label>
                            <input name="subtitle" type="text" className="form-control" placeholder="Enter a subtitle to create a subtitle in the article" />

                            <label htmlFor="categories">Categories:</label>
                            <select multiple name="categories" className="form-control">
                                {props.blogs.dashboardCategories.map((category: any) => {
                                    return <option value={category._id}>{category.title}</option>
                                })}
                            </select>
                            <label htmlFor="path">Path:</label>
                            <input name="path" type="text" className="form-control" placeholder="Enter a path for the article" />

                            <label htmlFor="cover">Cover Image:</label>
                            <input name="cover" type="text" className="form-control" placeholder="Enter a cover image for the article" />

                            <label htmlFor="tags">Tags:</label>
                            <input name="tags" type="text" className="form-control" placeholder="Enter tags seperated via comma" />

                            <label htmlFor="youtubeEmbedUrl">
                                Youtube Embed Url:
                            </label>
                            <input name="youtubeEmbedUrl" type="text" className="form-control" placeholder="Enter a youtube embed url" />
                            <button type='submit' className="btn btn-primary">
                                Gönderiyi Tamamla
                            </button>
                        </div>
                    </Col>
                    <Col lg={6} className="post-column-right">
                        <div className="floating-info">
                            HTML text'i girerek yazıyı oluşturabilirsiniz.
                        </div>
                        <div className='border'></div>
                        <textarea ref={sourceRef} onChange={(e: any) => {
                            //debouncedConvertMarkdownToHTML(e.target.value)
                            setResult(e.target.value)
                        }} className="textarea" placeholder='Enter HTML to create an article'></textarea>
                    </Col>
                </Row>
            </Container>
        </form>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(PostEditor);