import React, { useEffect } from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Navbar from "@components/Navbar";
import Footer from "@components/Footer";
import ScrollToTop from '@components/ScrollToTop';
import TwitchScreen, { TwitchChatScreen } from '@screens/MainStack/TwitchScreen';
//import CoursesScreen from "@screens/MainStack/CoursesScreen";
import CourseLoader from "@components/CourseLoader";
import Loader from "@components/Loader";
import DashboardStack from "./DashboardStack";
import HomeStack from "./HomeStack";
//import OpenSourceScreen from "@screens/MainStack/OpenSourceScreen";
const HomeScreen = React.lazy(() => import('@screens/MainStack/HomeScreen'));
const AboutScreen = React.lazy(() => import('@screens/MainStack/AboutScreen'));
const SocialScreen = React.lazy(() => import('@screens/MainStack/SocialScreen'));
const BlogsScreen = React.lazy(() => import('@screens/MainStack/BlogsScreen'));
const StackScreen = React.lazy(() => import('@components/Trader/Stack'));
const CurrencyScreen = React.lazy(() => import('@components/Trader/Currency'));
const OpenSourceScreen = React.lazy(() => import('@screens/MainStack/OpenSourceScreen'));
const NotFoundScreen = React.lazy(() => import('@screens/MainStack/NotFoundScreen'));
const CoursesScreen = React.lazy(() => import('@screens/MainStack/CoursesScreen'));
const BlogPostScreen = React.lazy(() => import('@components/BlogPost'));
const BlogPostNoSidebar = React.lazy(() => import('@components/BlogPostNoSidebar'));
const Videos = React.lazy(() => import('@components/Videos'));

const MainNavigation = (props: object) => {
    useEffect(() => {
        window.addEventListener("load", () => {
            const winObj: any = window;
            if (winObj?.Superpeer) {
                if (!(window.location.href.includes('/dashboard') || window.location.href.includes('/admin'))) {

                    new winObj.Superpeer.Widget({ embed: { type: "popup" }, launcher: { type: "profile-avatar", options: {} }, config: { username: "cangokceaslan", serviceSlug: "" } })
                }
            }
        })
    }, [])

    return (
        <div className="main">
            <React.Suspense fallback={<Loader loaded />}>
                <Router>
                    <Switch>
                        <Route path={["/dashboard", "/admin"]} component={DashboardStack} />
                        <Route path={"/"}>
                            <Navbar />
                            <ScrollToTop />
                            <HomeStack />
                            <Footer />
                        </Route>
                        {/* <Route>
                            <NotFoundScreen />
                        </Route> */}
                    </Switch>
                </Router>
            </React.Suspense>
        </div >

    )
}


export default MainNavigation;