import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Helmet from 'react-helmet';
import Navbar from "@components/Navbar";
import Footer from "@components/Footer";
import ScrollToTop from '@components/ScrollToTop';
import TwitchScreen, { TwitchChatScreen } from '@screens/MainStack/TwitchScreen';
//import CoursesScreen from "@screens/MainStack/CoursesScreen";
import CourseLoader from "@components/CourseLoader";
import Loader from "@components/Loader";
import DashboardNavigation from "./DashboardStack";
//import OpenSourceScreen from "@screens/MainStack/OpenSourceScreen";
const HomeScreen = React.lazy(() => import('@screens/MainStack/HomeScreen'));
const AboutScreen = React.lazy(() => import('@screens/MainStack/AboutScreen'));
const SocialScreen = React.lazy(() => import('@screens/MainStack/SocialScreen'));
const BlogsScreen = React.lazy(() => import('@screens/MainStack/BlogsScreen'));
const StackScreen = React.lazy(() => import('@components/Trader/Stack'));
const CurrencyScreen = React.lazy(() => import('@components/Trader/Currency'));
const OpenSourceScreen = React.lazy(() => import('@screens/MainStack/OpenSourceScreen'));
const NotFoundScreen = React.lazy(() => import('@screens/MainStack/NotFoundScreen'));
const CoursesScreen = React.lazy(() => import('@screens/MainStack/CoursesScreen'));
const BlogPostScreen = React.lazy(() => import('@components/BlogPost'));
const CalendarScreen = React.lazy(() => import('@screens/MainStack/CalendarScreen'));
const BlogPostNoSidebar = React.lazy(() => import('@components/BlogPostNoSidebar'));
const Videos = React.lazy(() => import('@components/Videos'));
const Video = React.lazy(() => import('@screens/MainStack/LatestVideoScreen'));

const HomeStack = (props: any) => {
    const prepareRouter = (route: string) => {
        return props?.match?.url + route;
    }
    return (
        <div className="home-stack-component-root">
            <Switch>
                <Route path={"/"} exact>
                    <HomeScreen />
                </Route>
                <Route path={prepareRouter("/twitch")}>
                    <TwitchScreen />
                </Route>
                <Route path="/twitch-chat">
                    <TwitchChatScreen />
                </Route>
                <Route path={["/social", "/sosyal"]}>
                    <SocialScreen />
                </Route>
                <Route path={["/about", "/hakkimda"]}>
                    <AboutScreen />
                </Route>
                <Route path={["/courses", "/kurslar"]}>
                    <CoursesScreen />
                </Route>
                <Route path={["/blogs", "/yazilarim"]}>
                    <BlogsScreen />
                </Route>
                <Route path={["/yazi/:id", "/post/:id"]}>
                    {/* <BlogPostScreen /> */}
                    <BlogPostNoSidebar />
                </Route>
                <Route path={["/projects", "/projelerim", "/open-source", "/acik-kaynak"]}>
                    <OpenSourceScreen />
                </Route>
                <Route path={["/course/:key", "/courses/:key"]}>
                    <CourseLoader />
                </Route>
                <Route path={["/video/latest"]}>
                    <Video />
                </Route>
                <Route path={["/calendar"]}>
                    <CalendarScreen />
                </Route>
                <Route path={["/video/:id", "/watch/:id", "/izle/:id"]}>
                    {/* <BlogPostScreen /> */}
                    <Videos />
                </Route>

                {/* <Route path="/trade">
                            <Route path={["/trade/:key"]}>
                                <TraderScreen />
                            </Route>
                        </Route> */}
                <Route path="/stack">
                    <Route path={["/stack/:stack/:key"]}>
                        <StackScreen />
                    </Route>
                </Route>
                <Route path="/currency">
                    <Route path={["/currency/:stack/:key"]}>
                        <CurrencyScreen />
                    </Route>
                </Route>
                {/* <Route>
                <NotFoundScreen />
            </Route> */}
            </Switch>
        </div>
    )
}


export default HomeStack;