import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { getPostsDashboard } from '@modules/blogs';
import { Link } from 'react-router-dom';
import { AiFillFileAdd } from 'react-icons/ai';
import './index.scss';
import _ from 'lodash';

const mapStateToProps = (state: any) => ({
    dashboardPosts: state.blogs.dashboardPosts
})

const mapActionsToProps = {
    getPostsDashboard: getPostsDashboard
}

const Posts: React.FunctionComponent<any> = (props) => {
    const btnRef = React.useRef<HTMLButtonElement>(null);
    const [posts, setPosts] = useState<any>([]);

    useEffect(() => {
        const apiKey = localStorage.getItem('apiKey');
        if (!apiKey) {
            window.location.href = "/dashboard/login";
        }

        props.getPostsDashboard({
            searchText: undefined
        });

    }, [])

    const handleSubmit = (e: any) => {
        e.preventDefault();
        const searchText = e.target[0].value;

        if (searchText === '') {

            props.getPostsDashboard({
                searchText: undefined
            });
        } else {
            props.getPostsDashboard({
                searchText: searchText
            });
        }


    }

    const debouncedOnSubmit = _.debounce((e: any) => {
        if (btnRef.current) {
            btnRef.current.click()
        }
    }, 1000)

    return (
        <div className="posts-container container">
            <div className="header container">
                <h1>Posts</h1>
                <div className="add-post">
                    <Link to="/dashboard/posts/create">
                        Yeni post ekle
                        <AiFillFileAdd size={40} color={"red"} />
                    </Link>
                </div>
            </div>
            <form onSubmit={handleSubmit}>
                <div className="post-search">
                    <input onChange={debouncedOnSubmit} type="text" placeholder="Post ara" className='search-input' />
                    <button ref={btnRef} className="search-button">
                        Yazı Ara
                    </button>
                </div>
            </form>

            <div className="posts">
                {props?.dashboardPosts.map((post: any) => {
                    return (
                        <div className="post card" onClick={() => {
                            window.location.href = `/dashboard/editor/${post._id}`;
                        }}>
                            <img src={post.cover} className="post-cover" />
                            <div className="post-title">
                                {post.title}
                            </div>
                            <div className="post-link">
                                <Link to={`/dashboard/editor/${post._id}`}>Post'u düzenle</Link>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(Posts);