import React, { useEffect, useRef, useState } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import './index.scss';
import showdown from 'showdown';
import _ from 'lodash';
import { BsPlus, BsToggleOff, BsToggleOn } from 'react-icons/bs';

function scrollToBottom(element) {
    element.scroll({ top: element.scrollHeight, behavior: 'smooth' });
}

const PhotoUrlCreator: React.FunctionComponent<any> = (props) => {
    const [nodes, setNodes] = useState<number>(5);
    const [urls, setUrls] = useState<string[]>([]);
    const [calculatedUrls, setCalculatedUrls] = useState<string[]>([]);
    return (
        <div className="photo-url-popup" id="photo-url-popup">
            {Array(nodes).fill(1).map((node, index) => {
                return (
                    <React.Fragment>
                        <div role="button" onClick={() => {
                            const arr = urls.filter((url, index) => {
                                return url;
                            }).map((url, index) => {
                                const fileKey = url.replace("https://drive.google.com/file/d/", "").replace("/view?usp=sharing", "");
                                return "https://lh3.googleusercontent.com/d/" + fileKey
                            })

                            setCalculatedUrls(arr);
                        }} className="btn popup-saver">
                            Linkleri Dönüştür
                        </div>
                        <div>
                            <div className="form-group d-flex">
                                <b className="photo-url-label">Google Drive URL:</b>
                                <div>
                                    <input type="text" onChange={(e: any) => {
                                        urls[index] = e.target.value
                                        setUrls(urls);
                                    }} className="photo-input form-control" id="inputPassword" placeholder="Google Drive URL" />
                                </div>
                                {(nodes - 1 === index) && (
                                    <button onClick={() => {
                                        setNodes(nodes + 1);
                                        scrollToBottom(document.getElementById("photo-url-popup"))
                                    }} className='btn btn-primary add-input'>
                                        <BsPlus /> Yeni Ekle
                                    </button>
                                )}
                            </div>
                        </div>
                    </React.Fragment>
                )
            })}
            <div className="photos">
                {calculatedUrls.map((url, index) => {
                    return (<div>{index + 1}. <a href={url} target="__blank">{url}</a></div>)
                })}
            </div>
        </div >
    )
}


const Blogs: React.FunctionComponent<any> = (props) => {
    const [togglePopup, setTogglePopup] = useState<boolean>(false);
    const sourceRef = useRef<HTMLTextAreaElement>(null);
    const resultRef = useRef<HTMLTextAreaElement>(null);
    const [toggler, setToggler] = React.useState<boolean>(true);
    const [togglerChanged, setTogglerChanged] = React.useState<boolean>(false);
    const [source, setSource] = React.useState<string>("");
    const [result, setResult] = React.useState<string>("");

    const convertMarkDownToHTML = (text: string) => {
        var converter = new showdown.Converter({
            metadata: true,
            simpleLineBreaks: false,
            openLinksInNewWindow: true,
            emoji: true
        }),
            html = converter.makeHtml(text);
        setSource(text);
        setResult(html);
        return html;
    };

    const debouncedConvertMarkdownToHTML = _.debounce(convertMarkDownToHTML, 500);

    const convertHTMLToMarkdown = (html: string) => {
        var converter = new showdown.Converter({
            metadata: true,
            simplifiedAutoLink: false,
            simpleLineBreaks: false,
            excludeTrailingPunctuationFromURLs: false,
            openLinksInNewWindow: true,
            emoji: true
        }),
            md = converter.makeMarkdown(html);
        setSource(md);
        setResult(md);
        return md;
    };
    const debouncedConvertHTMLToMarkdown = _.debounce(convertHTMLToMarkdown, 500);

    useEffect(() => {
        if (togglerChanged === true) {
            const tmpSource: any = new String(source);
            const tmpResult: any = new String(result);
            if (sourceRef && sourceRef?.current) {
                sourceRef.current.value = tmpResult;
            }
            if (resultRef && resultRef?.current) {
                resultRef.current.value = tmpSource;
            }
            setSource(tmpResult);
            setResult(tmpSource);
        }

    }, [toggler])

    return (
        <div className="blog-editor">
            <div className="blog-image-editor">

            </div>
            <Container fluid className="blog-container">
                <Row className="blog-row">
                    <Col lg={6} className="blog-column-left">
                        <div className="floating-info">
                            {toggler ? "Markdown" : "HTML"} to {!toggler ? "Markdown" : "HTML"} Converter
                        </div>
                        <div className='border'></div>
                        <textarea ref={sourceRef} onChange={(e: any) => {
                            if (toggler === true) {
                                debouncedConvertMarkdownToHTML(e.target.value)
                            } else {
                                debouncedConvertHTMLToMarkdown(e.target.value)
                            }
                        }} className="textarea"></textarea>
                    </Col>
                    <Col lg={6} className="blog-column-right">
                        <textarea ref={resultRef} disabled className="textarea" value={result}></textarea>
                    </Col>
                </Row>
            </Container>
            <div className={`toggler ${toggler ? "b-green" : "b-red"}`} onClick={async () => {
                setTogglerChanged(true);
                setToggler(!toggler);
            }}>
                <div>
                    {toggler ? <BsToggleOn size="40px" /> : <BsToggleOff size="40px" />}
                </div>
            </div>
            <div role="button" onClick={() => {
                setTogglePopup(!togglePopup)
            }} className="btn popup-toggler">
                Fotoğraflar'ı {togglePopup ? "Kapat" : "Aç"}
            </div>
            {togglePopup && <PhotoUrlCreator />}
        </div>
    )
}

export default Blogs;