import api from '@clients/api.client';
import lodash from 'lodash';

export const GET_POSTS = "BLOGS/GET_POSTS";
export const GET_POSTS_SUCCESS = "BLOGS/GET_POSTS_SUCCESS";
export const GET_POSTS_FAILED = "BLOGS/GET_POSTS_FAILED";

export const GET_POSTS_API = "BLOGS/GET_POSTS_API";
export const GET_POSTS_API_SUCCESS = "BLOGS/GET_POSTS_API_SUCCESS";
export const GET_POSTS_API_FAILED = "BLOGS/GET_POSTS_API_FAILED";

export const GET_POSTS_DASHBOARD = "BLOGS/GET_POSTS_DASHBOARD";
export const GET_POSTS_DASHBOARD_SUCCESS = "BLOGS/GET_POSTS_DASHBOARD_SUCCESS";
export const GET_POSTS_DASHBOARD_FAILED = "BLOGS/GET_POSTS_DASHBOARD_FAILED";

export const GET_CATEGORIES = "BLOGS/GET_CATEGORIES";
export const GET_CATEGORIES_SUCCESS = "BLOGS/GET_CATEGORIES_SUCCESS";
export const GET_CATEGORIES_FAILED = "BLOGS/GET_CATEGORIES_FAILED";

export const CREATE_POST = "BLOGS/CREATE_POST";
export const CREATE_POST_SUCCESS = "BLOGS/CREATE_POST_SUCCESS";
export const CREATE_POST_FAILED = "BLOGS/CREATE_POST_FAILED";

export const getPostsFromApi = (payload = {}) => (dispatch, getStore) => {
    if (getStore().blogs.fetchingApi === true) return;
    if (getStore().blogs.apiPostsEnd === true) return;
    const page = getStore().blogs.apiPostsPage;
    dispatch({ type: GET_POSTS_API, payload })
    return api.get(`/posts?page=${page}`)
        .then((response: any) => {
            console.log(response.data);
            dispatch({
                type: GET_POSTS_API_SUCCESS, payload: {
                    posts: response.data?.node?.posts
                }
            })
            return response.data;
        })
        .catch(function (error) {
            dispatch({
                type: GET_POSTS_API_FAILED,
                payload: error
            })
            console.log(error);
        });
}

