import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Login from "@screens/Dashboard/pages/Login";
import Editor from "@screens/Dashboard/pages/Editor";
import EnterApiKey from "@screens/Dashboard/pages/EnterApiKey";
import Posts from "@screens/Dashboard/pages/Posts";
import PostEditor from "@screens/Dashboard/pages/PostEditor";

const DashboardStack: React.FunctionComponent<any> = (props) => {
    const prepareRouter = (route: string) => {
        return props?.match?.url + route;
    }
    return (
        <Switch >
            <Route path={prepareRouter("/editor")} exact={true}>
                <Editor />
            </Route>
            <Route path={prepareRouter("/login")} exact={true}>
                <EnterApiKey />
            </Route>
            <Route path={prepareRouter("/posts")} exact={true}>
                <Posts />
            </Route>
            <Route path={prepareRouter("/login")} exact={true}>
                <Login />
            </Route>
            <Route path={prepareRouter("/editor/634ad4f69db843a3eb8a1ad9")}>
                <PostEditor />
            </Route>
        </Switch>

    )
}


export default DashboardStack;