const prepareNavbarContent = (history, pathname, onPush) => {
    return Object.keys(NavbarData).map((navbarItem, index) => {
        const CurrentItem = NavbarData[navbarItem];
        return (
            <div className={`navbar-bottom-flex ${CurrentItem?.pathname === pathname && "active"}`} onClick={(e) => {
                if (CurrentItem?.internal) {
                    history.push(CurrentItem.pathname);
                } else {
                    window.open(CurrentItem.pathname, '_blank');
                }
                onPush()
            }}>
                <CurrentItem.Icon size="24px" />
                <div
                    className='navbar-bottom-link'>
                    {CurrentItem?.title}
                </div>
            </div>
        )
    })
}

const prepareResponsiveNavbarContent = (menuRef, history, pathname, onPush) => {
    return Object.keys(NavbarData).map((navbarItem, index) => {
        const CurrentItem = NavbarData[navbarItem];
        return (
            <div className={`navbar-bottom-responsive-flex ${CurrentItem?.pathname === pathname && "active"}`} onClick={(e) => {
                menuRef.current.style.display = "none"
                menuRef.current.classList.remove('animate__slideInDown');
                menuRef.current.classList.add('animate__slideOutUp');
                if (CurrentItem?.internal) {
                    history.push(CurrentItem.pathname);
                } else {
                    window.open(CurrentItem.pathname, '_blank');
                }
                onPush()
            }}>
                <div>
                    <CurrentItem.Icon size="24px" />
                </div>
                <div id={CurrentItem?.id} className='navbar-bottom-responsive-link'>
                    {CurrentItem?.title}
                </div>
            </div>
        )
    })
}

const prepareFixedNavbarContent = (history, pathname, onPush) => {
    return Object.keys(NavbarData).filter((item: any, index: number) => {
        if (index === 0) {
            return false
        }
        return true;
    }).map((navbarItem, index) => {
        const CurrentItem = NavbarData[navbarItem];
        return (
            <div className={`navbar-bottom-flex ${CurrentItem?.pathname === pathname && "active"}`} onClick={(e) => {
                if (CurrentItem?.internal) {
                    history.push(CurrentItem.pathname);
                } else {
                    window.open(CurrentItem.pathname, '_blank');
                }
                onPush()
            }}>
                <div>
                    <CurrentItem.Icon size="24px" />
                </div>
                <div id={CurrentItem?.id} className='navbar-bottom-link'>
                    {CurrentItem?.title}
                </div>
            </div>
        )
    })
}



const NavbarToggler = (props) => {
    const { menuRef } = props;
    return (
        <div onClick={(e) => {
            menuRef.current.style.display = "block";
            if (menuRef.current.classList.contains('animate__slideInDown')) {
                menuRef.current.classList.remove('animate__slideInDown');
                menuRef.current.classList.add('animate__slideOutUp');
            } else if (menuRef.current.classList.contains('animate__slideOutUp')) {
                menuRef.current.classList.remove('animate__slideOutUp');
                menuRef.current.classList.add('animate__slideInDown');
            } else {
                menuRef.current.classList.remove('animate__slideOutUp');
                menuRef.current.classList.add('animate__slideInDown');
            }

        }} className="navbar-menu-toggler">
            <div className="navbar-menu-toggler-icon-container">
                <AiOutlineMenu style={{
                    marginTop: "10px"
                }} size={"25px"} />
            </div>
        </div>
    )
}

const Navbar = (props: Object) => {
    const history = useHistory();
    const [pathName, setPathName] = useState(history.location.pathname);
    const [scrollTop, setScrollTop] = useState(0);
    const [showFixedMenu, setShowFixedMenu] = useState(false);

    useEffect(() => {
        const onScroll = e => {
            setScrollTop(e.target.documentElement.scrollTop);
            if (e.target.documentElement.scrollTop > 190) {
                setShowFixedMenu(true)
            } else {
                setShowFixedMenu(false)
            }
        };
        window.addEventListener("scroll", onScroll);

        return () => window.removeEventListener("scroll", onScroll);
    }, [scrollTop]);
    const menuRef = React.useRef(null);
    return <nav className="navbar">
        <div className='container-fluid navbar-top-container'>
            <NavbarToggler menuRef={menuRef} />
            <div onClick={() => {
                window.location.href = "/"
            }} className="navbar-logo-container">
                <Link to={{
                    pathname: "/"
                }}>
                    <img className='logo' src={LogoImage} />
                </Link>
                {
                    //"https://www.emlakjet.com/static/img/logo.svg"
                }
            </div>
        </div>
        <div className='seperator'></div>
        <div className='container-fluid navbar-bottom-container'>
            <div className='container navbar-bottom-container' style={{ display: "flex" }}>
                {prepareNavbarContent(history, pathName, () => {
                    setPathName(history.location.pathname)
                })}
            </div>

        </div>
        <div ref={menuRef} className='container-fluid navbar-bottom-container-responsive animate__animated animate__fast'
            style={{
                display: "none"
            }}>
            {prepareResponsiveNavbarContent(menuRef, history, pathName, () => {
                setPathName(history.location.pathname)
            })}
        </div>
        {/* <div className='container-fluid navbar-fixed-wrapper' style={{
            visibility: showFixedMenu ? "visible"
                : "hidden"
        }}>
            <div className='container navbar-bottom-container-fixed'>
                <Link to={{
                    pathname: "/"
                }} className="logo-container navbar-bottom-flex false">
                    <img className='logo' src={LogoImage} />
                </Link>
                {prepareFixedNavbarContent(history, pathName, () => {
                    setPathName(history.location.pathname)
                })}
            </div>
        </div> */}


    </nav>
}

export default Navbar;