import { withRouter } from "react-router";
import React, { useEffect, useState } from "react";
import UdemyCoursesData from '@data/courses';
import { urlFromItem } from '@components/Courses/index';
import '@components/CourseLoader/index.scss';
import DocumentSettings from "@utils/seo";
const courses = {};
const CourseUrls = {};

const seo = {
    title: "Kursa Yönlendiriliyor | Can Gökçeaslan",
    description: "Bu sayfayı kullanarak istediğin kursa yönlendirileceksin.",
    keywords: "cangokceaslan, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim, Hakkımda, About Me",
    author: "Can Gökçeaslan",
    locale: "tr",
    image: "https://www.cangokceaslan.com/",
    siteName: "https://www.cangokceaslan.com/course/*"
}
UdemyCoursesData.forEach((course, index) => {
    const computedUrl = urlFromItem(course);
    courses[course.id] = {
        ...course,
        computedUrl: computedUrl
    };
    CourseUrls[course.id] = computedUrl
});

function CourseLoader(props: { match: any; }) {
    const [loaded, setLoaded] = useState<boolean>(false);
    function runVisitCourse() {
        const key = props?.match?.params?.key;
        if (CourseUrls?.[key] !== undefined) {
            window.location.href = CourseUrls[key];
        } else {
            window.location.href = Object.keys(CourseUrls)[0];
        }
    };
    useEffect(() => {
        setLoaded(true);
        setTimeout(() => {
            runVisitCourse();
        }, 1200)
    }, []);
    if (!loaded) {
        return <div>Loading...</div>;
    }
    return (
        <div className="course-loader">
            <DocumentSettings docData={seo} />
            <div className="d-flex justify-content-center align-items-center">
                <div className="spinner-border text-dark spinner-loader" role="status">
                    <span className="visually-hidden">Loading...</span>
                </div>
            </div>
            <div className="text-center" style={{ marginTop: "30px", height: 60 }}>
                <h1 className="text-dark" style={{ fontSize: 35 }}>Kursa gidiyorsun</h1>
                <p className="text-dark">Çok kısa bir süre içinde kursa yönlendirileceksin...</p>
            </div>
        </div>
    );
}
export default withRouter(CourseLoader);